import React from "react";
import styled from "styled-components";

const Nav = styled.nav`
  background-color: #999;
  height: 50px;
  font-size: 14px;
  letter-spacing: 2px;
  font-family: "Open Sans", sans-serif, "google";
`;

const Navbar = styled.ul`
  display: none;
  @media (min-width: 1170px) {
    display: flex;
  }

  height: 50px;
  margin: 0;
  padding: 0;
  justify-content: center;
  align-items: center;
  list-style-type: none;

  li {
    margin: 1.2em;
    text-transform: uppercase;
    a:link,
    a:active,
    a:hover,
    a:visited {
      color: black;
      font-weight: bold;
      text-decoration: none;
    }
  }
`;

const LINKS = [
  { title: "Home", href: "/" },
  { title: "Karte", href: "/karte/" },
  { title: "FAQ", href: "/" },
  { title: "Erfahrungsberichte", href: "/" },
  { title: "Allerlei", href: "/" },
  { title: "Kontakt", href: "/" }
];

const Hamburger = styled(({ className }) => {
  return (
    <div class={`${className}`}>
      <label for="mobile-menu-checkbox" class="hamburger-box">
        <div class="hamburger-inner"></div>
      </label>
    </div>
  );
})`
  height: 50px;
  width: 100%;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;

  &:hover {
    opacity: 0.7;
    cursor: pointer;
  }

  input:checked + & {
    &:hover {
      opacity: 0.7;
      cursor: pointer;
    }

    .hamburger-inner,
    .hamburger-inner::before,
    .hamburger-inner::after {
      background-color: white;
    }
  }

  .hamburger-box {
    width: 100%;
    height: 50px;
    display: inline-block;
    position: relative;
    top: 0px;
    cursor: pointer;
  }

  .hamburger-inner {
    display: block;
    top: 50%;
    margin-top: -2px;
    cursor: pointer;
  }
  .hamburger-inner,
  .hamburger-inner::before,
  .hamburger-inner::after {
    width: 40px;
    height: 4px;
    background-color: white;
    border-radius: 4px;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease;
  }
  .hamburger-inner::before,
  .hamburger-inner::after {
    content: "";
    display: block;
  }
  .hamburger-inner::before {
    top: -10px;
  }
  .hamburger-inner::after {
    bottom: -10px;
  }

  input:checked + & {
    .hamburger-inner::before {
      transform: translate3d(-8px, 0, 0) rotate(-45deg) scale(0.7, 1);
    }

    .hamburger-inner::after {
      transform: translate3d(-8px, 0, 0) rotate(45deg) scale(0.7, 1);
    }
  }
`;

const MobileMenu = styled(({ children, className }) => {
  return (
    <div className={className}>
      <input id="mobile-menu-checkbox" type="checkbox" />
      <Hamburger />
      <ul>{children}</ul>
    </div>
  );
})`
  width: 300px;
  left: 20px;

  z-index: 700000;
  position: relative;

  input {
    display: none;
    position: absolute;
    top: 0;
    left: 100px;
    width: 50px;
    height: 50px;
  }

  ul {
    background-color: #999;
    display: block;
    visibility: hidden;
    width: 100%;
    overflow: hidden;
    -ms-transform: translateX(-100%);
    transform: translateX(-100%);
    -ms-transform-origin: right;
    transform-origin: right;
    right: 0;
    top: -5px;
    position: relative;
    transition: all 0.5s cubic-bezier(0.6, -0.61, 0.38, 2.03);
  }

  input:checked ~ ul {
    visibility: visible;
    -ms-transform: translateY(0);
    transform: translateY(0);
    -ms-transform: translateX(0);
    transform: translateX(0);
    opacity: 1;
  }

  ul {
    padding: 0;
    margin: 0;

    li {
      -moz-osx-font-smoothing: grayscale;
      -webkit-font-smoothing: antialiased;
      font-family: "Source Sans Pro", HelveticaNeue-Light,
        "Helvetica Neue Light", "Helvetica Neue", Helvetica, Arial,
        "Lucida Grande", sans-serif;
      font-size: 16px;
      height: 49px;
      line-height: 1.5;
      list-style-image: none;
      list-style-position: outside;
      list-style-type: none;

      a:link,
      a:visited {
        font-size: 16px !important;
        line-height: 1.5 !important;
        border: none;
        border-bottom: 1px solid rgba(33, 33, 33, 0.1);
        color: #7e7d78;
        padding: 12px 35px;
        transition: color, 0.3s ease-out;
        text-decoration: none;
        display: block;
        position: relative;

        font-family: "Open Sans", sans-serif, "google";
        font-weight: 600;
        font-style: normal;
        color: #000;
        background-color: rgba(0, 0, 0, 0);

        &:hover {
          color: #0e4f5d;
        }
      }
    }
  }
  @media (min-width: 1170px) {
    display: none;
  }
`;

function Navigation({ page }) {
  return (
    <Nav>
      <Navbar>
        {LINKS.map(link => (
          <li>
            <a href={link.href}>{link.title}</a>
          </li>
        ))}
      </Navbar>
      <MobileMenu>
        {LINKS.map(link => (
          <li>
            <a href={link.href}>{link.title}</a>
          </li>
        ))}
      </MobileMenu>
    </Nav>
  );
}

export default Navigation;
